$gutter: 1rem;

$primary-color: #2784f4;
$secondary-color: #ffea29;
html {
  font-size: 14px;
  --swiper-navigation-color: #2784f4;
}
body {
  background-color: #f8f8f8;
}
.home,
.app-scene,
.product-system,
.chat-room-entrance,
.architecture-design,
.reason-to-choose,
.ecological-cooperation,
.about-us,
.our-customers,
.subscription-demo {
  position: relative;
  overflow: hidden;
  .container {
    margin-top: 2rem;
  }
}
.container {
  max-width: 1440px;
}
.primary-title {
  font-size: 1.2rem;
}
.secondary-title {
  color: var(--secondary);
}
.app-header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: #ffffff;
  .logo {
    display: inline-block;
    padding: .8rem 1rem;
    width: 120px;
  }
  .nav {
    display: none;
  }
  .nav-item {
    float: left;
    margin-left: 2rem;
    margin-right: 2rem;
    >a {
      color: var(--secondary);
      font-size: 1.1rem;
      text-decoration: none;

      transition: all .3s;
    }
    &:first-child {
      margin-left: 1rem;
    }
    &:last-child {
      margin-right: 1rem;
    }
    &:hover > a,
    &.selected > a {
      color: inherit;
    }
    &.selected {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 100%;
        width: 0;
        height: 0;
        margin-left: -4px;
        margin-top: 6px;
        display: block;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-bottom: 4px solid $primary-color;
      }
    }
  }
  .phone-icon {
    width: 20px;
    height: 20px;
  }
  .phone-number {
    padding-left: .5rem;
    padding-right: 1rem;
    font-size: 1.2rem;
    font-weight: 600;
  }
}
.home {
  .container {
    margin-top: 1rem;
  }
  .swiper {
    width: 100%;
    height: 230px;
  }
  .swiper-slide img {
    width: 100%;
    height: 100%;
  }
  .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    .bg {
      position: absolute;
      left: 135px;
      top: 0;
      z-index: -1;
      width: 45px;
      height: 45px;
      background: url('./src/assets/ic_home_top_bg.png') no-repeat center;
      background-size: 100%;
    }
    .title {
      width: 250px;
      height: auto;
    }
  }
  .overview {
    margin-top: 5rem;
    .line {
      width: 100%;
      height: 4px;
    }
    .label {
      font-size: 12px;
    }
    .number {
      font-size: 1.4rem;
      font-weight: 600;
    }
    .text {
      margin-bottom: .5rem;
      font-size: 12px;
      color: var(--secondary);
    }
  }
  .overview-item {
    &:nth-child(odd) {
      .line {
        background: linear-gradient(to right, $primary-color, mix(#FFFFFF, $primary-color, 20%));
      }
    }
    &:nth-child(even) {
      .line {
        background: linear-gradient(to right, $secondary-color, mix(#FFFFFF, $secondary-color, 20%));
      }
    }
  }
}
.app-scene {
  .item {
    width: 100%;
    overflow: hidden;
    height: 115px;
    transition: all .3s;
    margin-top: 1rem;
  }
  .item-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .title {
    white-space: pre-line;
    font-size: 1.3rem;
    font-weight: 600;
  }
  .text {
    font-size: .9rem;
    line-height: 2;
    opacity: 0;
    transition: all .3s;
  }
  .bg {
    opacity: 0;
    transition: all .3s;
  }
  .bg > span {
    display: inline-block;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    &:first-child {
      background: rgba($primary-color, .8);
    }
    &:last-child {
      background: rgba($secondary-color, .8);
      margin-left: -6px;
    }
  }
  .item-content {
    padding: 2rem;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.3);
    color: #ffffff;
  }
  .item:hover {
    height: 330px;
    .bg,
    .text {
      opacity: 1;
    }
  }
}
.product-system {
  .item {
    margin-top: 1rem;
    transition: all .3s;
    &:hover {
      transform: translateY(-10px) !important;
    }
  }
  .item-top {
    color: #fff;
    height: 125px;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 2rem;
  }
  .desc {
    color: rgba(255,255,255, 0.6);
  }
  .item-middle {
    background: #fff;
    padding-top: 2rem;
    ul {
      margin: 0;
      padding-bottom: 1px;
    }
    .label {
      font-size: 1.1rem;
      font-weight: 600;
    }
  }
  .item-bottom {
    background: #fff;
    padding: 1rem 2rem;
    button {
      height: 42px;
    }
  }
}
.our-customers {
  .r-logo {
    width: 146px;
    height: auto;
    margin-right: 16px;
    &:last-child {
      margin-right: 0;
    }
  }
}
.section-bg {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  &.chat-room-entrance {
    background-image: url('./src/assets/img_section1.jpg');
  }
  &.architecture-design {
    background-image: url('./src/assets/img_section5.png');
  }
  &.reason-to-choose {
    background-image: url('./src/assets/img_section3.png');
  }
  &.ecological-cooperation {
    background-image: url('./src/assets/img_section4.png');
  }
  &.subscription-demo {
    background-image: url('./src/assets/img_section2.jpg');
  }
}
.chat-room-entrance {
  .content {
    color: #ffffff;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  .btn {
    display: inline-block;
    background: transparent;
    padding: .7rem 3rem;
    color: #ffffff;
    border: 1px solid #ffffff;
    &:hover {
      border-color: $primary-color;
      color: $primary-color;
    }
  }
}
.subscription-demo {
  .content {
    color: #ffffff;
    width: 100%;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  .form-control {
    background-color: transparent;
    border-color: #ffffff;
    color: #ffffff;
    height: 40px;
    &:focus {
      box-shadow: none;
      border-color: #ffffff;
    }
    &::placeholder {
      color: #eeeeee;
    }
  }
  .input-group-text {
    background-color: #ffffff;
    border-color: #ffffff;
    color: $primary-color;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
.app-footer {
  padding-top: 2rem;
  background-color: #33373e;
  color: #777;
  .title {
    color: #ffffff;
  }
  .links {
    width: 50%;
    margin-bottom: 1rem;
  }
  .link {
    color: inherit;
    padding-top: .3rem;
    padding-bottom: .3rem;
  }
  .phone-number {
    color: #ffffff;
  }
  .code-item {
    text-align: center;
    width: 107px;
  }
}
.copyright {
  background-color: #33373e;
  color: #838589;
}
.about-us {
  .triangle {
    width: 0;
    height: 0;
    position: absolute;
    bottom: 0;
    z-index: 2;
  }
  .triangle-1 {
    border-width: 50px;
    border-style: solid;
    border-color: rgba($secondary-color, .5);
    border-top-color: transparent;
    border-right-color: transparent;
    right: 100px;
  }
  .triangle-2 {
    border-width: 80px;
    border-style: solid;
    border-color: rgba($primary-color, .5);
    border-top-color: transparent;
    border-left-color: transparent;
    right: 0;
  }
  .img-box {
    position: relative;
    padding-bottom: 3rem;
  }
  .img1 {
    width: 100%;
    position: relative;
    z-index: 1;
  }
  .img2 {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .sub-title {
    font-weight: 700;
    font-size: 1.2rem;
    color: #eeeeee;
  }
  .big-title {
    font-size: 6rem;
    font-weight: 600;
  }
  .desc {
    line-height: 1.8;
  }
  .btn {
    background: mix(#ffffff, $primary-color, 80%);
    color: $primary-color;
    padding: .6rem 2rem;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 100%;
      display: block;
      width: 0;
      opacity: 0;
      height: 3px;
      background: linear-gradient(to right, $secondary-color, mix(#ffffff, $secondary-color));
      transition: width .6s;
    }
    &.active,
    &:focus {
      box-shadow: none;
      background: $primary-color;
      color: #ffffff;
      &:after {
        width: 100%;
        opacity: 1;
      }
    }
  }
}
.architecture-design {
  .center-image {
    position: relative;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
  }
  .info {
    font-size: 12px;
    position: absolute;
    padding-top: 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    &:nth-of-type(1) {
      width: 120px;
      left: 57%;
      top: -10px;
    }
    &:nth-of-type(2) {
      width: 120px;
      right: 63%;
      top: 10%;
    }
    &:nth-of-type(3) {
      width: 140px;
      left: 60%;
      top: 27%;
    }
    &:nth-of-type(4) {
      width: 140px;
      right: 62%;
      top: 45%;
    }
    &:nth-of-type(5) {
      width: 130px;
      left: 58%;
      top: 65%;
    }
  }
  .dot {
    position: absolute;
    top: 100%;
    display: block;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background: #ffffff;
    box-shadow: 0 .25rem .5rem rgba(0,0,0,0.25);
    &::after {
      content: "";
      display: block;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background: $primary-color;
      position: absolute;
      left: 2px;
      top: 2px;
    }
  }
  .icon {
    width: 13px;
    height: 13px;
    margin-right: 5px;
  }
  .at-right {
    .dot {
      left: 0;
      transform: translateY(-50%);
    }
  }
  .at-left {
    .primary {
      flex-flow: row-reverse;
    }
    .icon {
      margin-left: 5px;
      margin-right: 0;
    }
    .dot {
      left: 100%;
      transform: translateY(-50%);
    }
  }
  .bottom-block {
    width: 100%;
  }
  .item {
    float: left;
    width: calc(50% - 0.5rem);
    background: #ffffff;
    padding: .5rem 1rem 1rem;
    margin-bottom: 1rem;
    &:nth-child(odd) {
      margin-right: 1rem;
    }
  }
  .title {
    img {
      width: 32px;
      height: 32px;
    }
  }
  .desc {
    font-size: 12px;
    color: #999;
    margin-bottom: 0;
  }
}
.reason-to-choose {
  .steps {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  .step {
    text-align: center;
    .tip {
      white-space: nowrap;
      position: relative;
      font-size: 12px;
      padding: 5px 6px;
      border-radius: .4rem;
      color: #ffffff;
      background: $primary-color;
      &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 100%;
        margin-left: -10px;
        margin-top: -2px;
        display: block;
        width: 0;
        height: 0;
        border: 10px solid $primary-color;
        border-left-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
      }
    }
    .cycle {
      margin-top: 20px;
      font-size: 12px;
      width: 22px;
      height: 22px;
      text-align: center;
      line-height: 22px;
      border-radius: 50%;
      background: $primary-color;
      color: #ffffff;
      display: inline-block;
      position: relative;
      z-index: 1;
    }
  }
  .line {
    width: 100%;
    height: 2px;
    background: $primary-color;
    position: absolute;
    left: 50%;
    bottom: 10px;
    z-index: 0;
  }
  .step {
    position: relative;
    width: 25%;
    & + .step {
      margin-left: 10px;
    }
    &:nth-child(even) {
      .line {
        background: $secondary-color;
      }
    }
    &:last-child {
      .line {
        display: none;
      }
    }
  }
  .step-cards {
    margin-left: -1rem;
    margin-right: -1rem;
    height: 250px;
    overflow: auto;
  }
  .step-card {
    background: url('./src/assets/bg_deliver_item.png') no-repeat;
    background-size: cover;
    height: 225px;
    width: 170px;
    flex-shrink: 0;
    display: flex;
    align-items: flex-end;
    & + .step-card {
      margin-left: 1rem;
    }
    img {
      width: 40px;
      height: 40px;
      margin: 1rem auto;
    }
    p {
      font-size: 12px;
      color: var(--secondary);
      padding: 1rem;
      margin-bottom: 0;
    }
    h2 {
      font-size: 1.1rem;
      text-align: center;
      margin-bottom: 0;
    }
    .content {
      width: 80%;
      height: 80%;
      border-radius: 0 70px 0 0;
      background: #ffffff;
    }
  }
}
.ecological-cooperation {
  .title {
    font-size: 1.2rem;
    position: relative;
    padding-bottom: 1rem;
    &:after {
      content: '';
      display: block;
      width: 30px;
      height: 2px;
      background: $primary-color;
      position: absolute;
      left: 0;
      top: 100%;
    }
    &.title-2 {
      margin-top: 2rem;
    }
  }
  .desc {
    color: #777;
    margin-top: 1.5rem;
  }
  .grid {
    display: grid;
    grid-gap: 1rem;
    &.grid-4 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
    }
    &.grid-2 {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  .grid-item {
    img {
      width: 100%;
    }
  }
  .hexagon-block {
    margin-bottom: 2rem;
    margin-top: 2rem;
    width: 100%;
    padding-top: 100%;
    background: url('./src/assets/bg_partner.png') no-repeat center center;
    background-size: 80%;
    position: relative;
  }
  .internal-hexagon {
    background: url('./src/assets/bg_partner_center.png') no-repeat center center;
    background-size: 100%;
    width: 150px;
    height: 150px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    .title-3::after {
      left: 50%;
      margin-left: -15px;
    }
  }
  .cycle {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    overflow: hidden;
    background: #ffffff;
    position: absolute;
    &.cycle-1 {
      left: 17%;
      top: 5%;
    }
    &.cycle-2 {
      right: 17%;
      top: 5%;
    }
    &.cycle-3 {
      left: 1%;
      top: 35%;
    }
    &.cycle-4 {
      right: 1%;
      top: 35%;
    }
    &.cycle-5 {
      left: 17%;
      bottom: 5%;
    }
    &.cycle-6 {
      right: 17%;
      bottom: 5%;
    }
    img {
      width: 100%;
    }
  }
}
.fade1-enter-active {
  animation: fadeInLeft .6s;
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-100%)
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media screen and (min-width: 768px) {
  .secondary-title {
    margin-bottom: 1rem;
  }
  .primary-title {
    font-size: 1.5rem;
  }
  .app-header {
    position: fixed;
    left: 0;
    right: 0;
    .logo {
      padding-top: 1.2rem;
      padding-bottom: 1.2rem;
    }
    .nav {
      display: block;
    }
  }
  .home {
    .swiper {
      height: auto;
    }
    .container {
      margin-top: 6rem;
    }
    .content {
      .bg {
        left: 360px;
        width: 125px;
        height: 125px;
      }
      .title {
        width: 730px;
      }
      .sub-title {
        font-size: 1.2rem;
        margin-top: 2rem;
        padding-left: 1.5rem;
        line-height: 60px;
        background: url('./src/assets/ic_home_top_bg2.png') no-repeat left center;
        background-size: auto 60px;
      }
    }
    .overview {
      width: 66%;
      margin-top: 26rem;
    }
    .overview-item {
      flex: 1;
      margin-right: 2rem;
      .line {
        height: 3px;
      }
      .label {
        font-size: 1rem;
      }
      .text {
        margin-top: .5rem;
        margin-bottom: 1rem;
      }
      .number {
        font-size: 3rem;
      }
    }
  }
  .app-scene {
    .item {
      width: 25%;
      height: 488px;
    }
    .item-content {
      padding: 10rem 2rem;
    }
    .item:hover {
      height: 488px;
    }
    .item:hover,
    .item.active {
      width: 530px;
      .bg,.text {
        opacity: 1;
      }
    }
  }
  .app-footer {
    padding-bottom: 2rem;
    .col-12 {
      border-left: 1px solid rgba(#838589, .3);
      &:first-child {
        border: 0;
      }
    }
    .links {
      width: 100%;
    }
  }
  .product-system {
    padding-bottom: 3rem;
    .item {
      margin-left: 1rem;
    }
    .col-12:first-child .item {
      margin-left: 0;
    }
  }
  .about-us {
    padding-bottom: 5rem;
    .triangle-1 {
      border-width: 100px;
      right: 200px;
    }
    .triangle-2 {
      border-width: 150px;
    }
    .img2 {
      left: -3rem;
    }
    .tabs-button {
      margin-top: 4rem !important;
    }
    .btn {
      padding: .6rem 5rem;
      font-size: 1.1rem;
      margin-right: 1rem;
    }
    .img-box {
      padding-bottom: 5rem;
      width: 530px;
      float: right;
      margin-top: -28%;
    }
    .desc,
    .tabs-content {
      width: 50%;
      margin-top: 1rem;
      font-size: 1.1rem;
    }
    .text-gray {
      color: #999999;
    }
    .desc-title {
      position: relative;
      width: 190px;
      &:after {
        content: '';
        display: block;
        width: 30px;
        height: 30px;
        background: url('./src/assets/ic_arrow_right.png') no-repeat center center;
        background-size: 100% auto;
        position: absolute;
        left: 100%;
        top: 0;
      }
    }
  }
  .architecture-design {
    .center-image {
      width: 500px;
    }
    .secondary {
      color: #999;
      margin-top: 10px;
      margin-bottom: 5px;
    }
    .dot {
      width: 12px;
      height: 12px;
      &::after {
        width: 8px;
        height: 8px;
      }
    }
    .icon {
      width: 15px;
      height: 15px;
    }
    .info {
      font-size: 1rem;
      .primary {
        font-size: 1.2rem;
      }
      &:nth-of-type(1) {
        width: 444px;
        left: 47%;
        top: 13px;
      }
      &:nth-of-type(2) {
        width: 340px;
        right: 63%;
        top: 10%;
      }
      &:nth-of-type(3) {
        width: 440px;
        left: 70%;
        top: 21%;
      }
      &:nth-of-type(4) {
        width: 520px;
        right: 55%;
        top: 45%;
      }
      &:nth-of-type(5) {
        width: 430px;
        left: 58%;
        top: 59%;
      }
    }
    .at-right {
      .secondary {
        text-align: right;
      }
      .icon {
        margin-right: 0;
        margin-left: 5px;
      }
      .primary {
        flex-flow: row-reverse;
        justify-content: flex-start !important;
      }
    }
    .item {
      font-size: 1.2rem;
      width: calc(25% - 30px);
      margin-right: 40px;
      padding: 1rem;
      margin-bottom: 0;
      &:nth-child(odd) {
        margin-right: 40px;
      }
      &:last-child {
        margin-right: 0;
      }
    }
    .desc {
      font-size: 1rem;
    }
    .bottom-block {
      position: relative;
      width: 80%;
      margin-left: auto;
      margin-right: auto;
      margin-top: 5rem;
      margin-bottom: 10rem;
    }
    .prefix,
    .affix {
      position: absolute;
      border-left: 1px dashed rgba(0,0,0,0.1);
      border-right: 1px dashed rgba(0,0,0,0.1);
      .to-top {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        transform: translateY(-50%);
      }
      .top-line {
        border-top: 1px dashed rgba(0,0,0,0.1);
        flex: 1;
      }
      .top-text {
        white-space: nowrap;
        margin-left: 10px;
        margin-right: 10px;
        font-size: 1.1rem;
        line-height: 1;
      }
    }
    .prefix {
      left: calc((25% - 30px) / 2);
      top: -50px;
      right: calc((25% - 30px) / 2);
      height: 50px;
    }
    .affix {
      height: 40px;
      top: 100%;
      .to-top {
        top: 100%;
      }
      .disc {
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: $primary-color;
      }
    }
    .affix-1 {
      width: 25%;
      left: calc((25% - 30px) * 0.55);
    }
    .affix-2 {
      width: 25%;
      left: calc(50% - 130px);
    }
    .affix-3 {
      width: 25%;
      left: calc(75% - 115px);
    }
  }
  .ecological-cooperation {
    .cycle {
      width: 168px;
      height: 168px;
    }
    .desc {
      font-size: 12px;
    }
    .title-1 {
      margin-top: 5rem;
    }
    .title-2 {
      margin-top: 5rem !important;
    }
    .details {
      margin-top: 2rem;
    }
    .grid {
      grid-gap: 2rem;
    }
    .internal-hexagon {
      width: 320px;
      height: 320px;
      flex-direction: column;
      .desc {
        padding-left: 5rem;
        padding-right: 5rem;
      }
    }
  }
  .reason-to-choose {
    .container {
      position: relative;
      margin-top: 5rem;
    }
    .steps {
      width: 600px;
      overflow: hidden;
      position: absolute;
      right: 0;
      top: 0;
      margin-top: 0;
    }
    .step {
      .tip {
        margin-left: auto;
        margin-right: auto;
        width: 80px;
        font-size: 1.1rem;
      }
      .cycle {
        width: 30px;
        height: 30px;
        font-size: 1rem;
        line-height: 30px;
        box-shadow: 0 0 1rem rgba(0,0,0,0.2);
      }
    }
    .line {
      bottom: 13px;
    }
    .step-cards {
      width: 100%;
      height: 400px;
      margin-top: 5rem;
      .step-card {
        flex: 1;
        height: 340px;
        transition: all .3s;
        &:hover {
          transform: translateY(-10px);
        }
        .content {
          align-items: center;
          justify-content: center;
          h2 {
            font-size: 1.4rem;
          }
          p {
            font-size: 1rem;
          }
        }
      }
    }
  }
  .subscription-demo {
    .content {
      h5 {
        font-size: 1.5rem;
      }
      p {
        font-size: .9rem;
      }
      .input-group {
        width: 340px;
        margin-left: auto;
        margin-right: auto;
      }
      .input-group-text {
        padding-left: 2rem;
        padding-right: 2rem;
      }
    }
  }
  .single-line {
    border-top: 1px solid rgba(#838589, .3);
  }
  .our-customers {
    .r-logo {
      width: 235px;
    }
    .primary-title,
    .secondary-title {
      text-align: center;
    }
    .secondary-title {
      font-size: 1.5rem;
      color: inherit;
    }
    .primary-title {
      font-size: 1rem;
      color: var(--secondary);
    }
  }
}
